body {
  padding: 65px;
}

input{
  font-size: 1.1rem;
  
}

.card-grid{
  display:flex;
  flex-direction:row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px; 
}

.card img {
  max-height: 170px;
}